<template>
  <div style="width: 100%; height: 80vh">
    <div
      style="
        width: 98%;
        height: 27%;
        border: 1px solid rgb(233, 233, 233);
        display: flex;
      "
      v-if="data"
    >
      <div class="left_div">
        <div class="span_div" style="background-color: rgb(93, 172, 255)">
          <span style="font-size: 30px" class="el-icon-office-building"></span>
        </div>
        <p>{{ data.num }}</p>
        <p>子企业个数</p>
      </div>
      <div class="left_div">
        <div class="span_div" style="background-color: rgb(255, 171, 145)">
          <span style="font-size: 30px" class="el-icon-user-solid"></span>
        </div>
        <p>{{ data.maxnum }}</p>
        <p>最大坐席数</p>
      </div>
      <div class="left_div">
        <div class="span_div" style="background-color: rgb(80, 223, 195)">
          <span style="font-size: 30px" class="el-icon-user"></span>
        </div>
        <p>{{ data.maxconcurrency }}</p>
        <p>实际坐席总数</p>
      </div>
      <div class="left_div">
        <div class="span_div" style="background-color: rgb(255, 145, 113)">
          <span style="font-size: 30px" class="el-icon-s-custom"></span>
        </div>
        <p>{{ data.maxnum }}</p>
        <p>最大登录并发总数</p>
      </div>
      <div class="left_div">
        <div class="span_div" style="background-color: rgb(131, 232, 172)">
          <span style="font-size: 30px" class="el-icon-finished"></span>
        </div>
        <p>{{ data.maxnum }}</p>
        <p>停用坐席总数</p>
      </div>
      <div class="left_div">
        <div class="span_div" style="background-color: rgb(254, 212, 98)">
          <span style="font-size: 30px" class="el-icon-phone"></span>
        </div>
        <p>{{ data.maxnum }}</p>
        <p>预测式外呼坐席并发总数</p>
      </div>
    </div>
    <div v-else></div>
    <div
      style="
        width: 98%;
        height: 50%;
        margin-top: 3%;
        border: 1px solid rgb(233, 233, 233);
      "
    >
      <div
        style="
          width: 100%;
          height: 6vh;
          display: flex;
          font-size: 2.5vh;
          line-height: 4vh;
          border-bottom: 1px solid rgb(233, 233, 233);
          display: flex;
          align-items: center;
        "
      >
        <div style="margin-left: 3%">超级企业信息</div>
        <div style="margin-right: 3%">
          当前超级企业为{{ state }}
          <button
            @click="getState"
            style="
              width: 12vh;
              height: 6vh;
              border: none;
              background-color: rgb(255, 255, 255);
            "
          >
            <u style="color: rgb(42, 171, 255)">停用超级企业</u>
          </button>
        </div>
      </div>
      <div style="width: 100%; height: 92%; display: flex" v-if="data">
        <div class="bottom_div">
          <div class="bottom_div_div">
            <div class="div_show_left">超级企业名称</div>
            <div class="div_show">{{ data.ename }}</div>
          </div>
          <div class="bottom_div_div">
            <div class="div_show_left">企业域名</div>
            <div class="div_show">{{ data.ename }}</div>
          </div>
          <div class="bottom_div_div">
            <div class="div_show_left">超级管理员账号</div>
            <div class="div_show">
              {{ data.adminname }}
            </div>
          </div>
          <div class="bottom_div_div">
            <div class="div_show_left">邮箱</div>
            <div class="div_show">
              {{ data.adminemil }}
            </div>
          </div>
        </div>
        <div class="bottom_div">
          <div class="bottom_div_div">
            <div class="div_show_left">超级企业ID</div>
            <div class="div_show">{{ data.num }}</div>
          </div>
          <div class="bottom_div_div">
            <div class="div_show_left">最大坐席数</div>
            <div class="div_show">{{ data.maxnum }}</div>
          </div>
          <div class="bottom_div_div">
            <div class="div_show_left">最大登录并发总数</div>
            <div class="div_show">{{ data.maxconcurrency }}</div>
          </div>
          <div class="bottom_div_div">
            <div class="div_show_left">创建时间</div>
            <div class="div_show">{{ formatDate(data.create_time) }}</div>
          </div>
        </div>
      </div>
      <div v-else></div>
    </div>
  </div>
</template>

<script>
import { getSuper, getState } from "../../api/login_http";
import { mapState } from "vuex"; // 导入 mapState 函数

export default {
  data() {
    return {
      data: null,
      getid: { id: localStorage.getItem("id") },
      state: "启用状态",
    };
  },
  computed: {
    ...mapState({
      params: (state) => state.params, // 使用 mapState 获取 Vuex 中的参数
    }),
  },
  created() {
    if (this.getid != "") {
      this.getSuper(this.getid);
    } else {
      this.getSuper(); // 调用获取业务数据的方法
      const id = localStorage.getItem("id");
      console.log(id);
    }
  },
  watch: {
    params: {
      handler(newParams) {
        // 监听参数变化
        const id = { id: newParams };
        this.getSuper(id); // 根据新参数获取数据
      },
      deep: true, // 深度监听
    },
  },
  methods: {
    getSuper(id) {
      if (this.state == "启用状态") {
        this.state = "停用状态";
      } else {
        this.state = "启用状态";
      }
      getSuper(id).then((res) => {
        this.data = res.data;
        if (this.data.status == 1) {
          this.state = "启用状态";
        } else {
          this.state = "停用状态";
        }
      });
    },
    formatDate(timestamp) {
      // 如果时间戳是以秒为单位的，将其乘以 1000 转换为毫秒
      const milliseconds = timestamp * 1000;
      const date = new Date(milliseconds);
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, "0"); // Month starts from 0
      const day = String(date.getDate()).padStart(2, "0");
      return `${year}-${month}-${day}`;
    },
    getState() {
      const obj = { id: this.data.id, status: !this.data.status };
      getState(obj).then(() => {
        if (this.state == "启用状态") {
          this.state = "停用状态";
        } else {
          this.state = "启用状态";
        }
      });
    },
  },
};
</script>

<style scoped>
p {
  white-space: nowrap; /* 不换行 */
  overflow: hidden; /* 隐藏溢出部分 */
  text-overflow: ellipsis; /* 显示省略号 */
  width: 120px; /* 设置固定宽度 */
}

.span_div {
  width: 4em;
  height: 4em;
  border-radius: 50%;
  display: flex;
  align-items: center;
  color: white;
}

.div_show {
  width: 40%;
  text-align: left;
  margin-left: 10%;
}

.div_show_left {
  width: 50%;
  height: 2vh;
  text-align: right;
}

.left_div {
  width: 15%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.bottom_div {
  width: 50%;
  height: 77%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.bottom_div_div {
  width: 100%;
  display: flex;
  margin-top: 3vh;
}
</style>
